import * as React from 'react';
import { IconButton } from './Basics';
import type { IIconButtonProps } from './Basics';
import { IconUnderline } from '../icons';

export function UnderlineButton(props: IIconButtonProps) {
  const {
    icon = <IconUnderline />,
    ...rest
  } = props;

  return (
    <IconButton
      testid="underline_button"
      icon={icon}
      {...rest}
    />
  )
}